.wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.header {
    margin: 10px;
    font-size: large;
    color: #282c34;

}

.table {
    border-collapse: separate;
    border-spacing: 10px;
}

.cell {
    color: blueviolet;
    font-size: 20px;
    padding-right: 50px;
}

.haver {
    width: 200px;
}

table {
    border-collapse: collapse;
}

th, td {
    border: 2px solid darkcyan;
    border-radius: 5px;
    background-color: moccasin;
}

.btn {
    display: inline-block;
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    text-decoration: none;
    border: none;
    border-radius: 5px;
    background-color: #61dafb;
    color: white;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-top: 10px ;
}

.btn:hover {
    background-color: #48bbd8;
}

.AddCont {
    text-align: center;
    border-radius: 10px;
}

input {
    width: 80%;
    padding: 7px;
    border: 1px solid #ccc;
    font-size: 14px;
}

.DeleteCont {
    margin: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 250px;
}